<template>
  <div ref="bg">
    <div class="count">{{ h_count }}</div>
    <component
      v-for="(item, key) in obj"
      :key="key"
      :ref="key"
      :is="item.component"
      @end_anim="deleteItem"
      :props_data="item.emoji"
    >
    </component>
    <div ref="items" class="itmes">
      <div ref="glist" class="grid">
        <v-row>
          <v-col v-for="n in 9" :key="n" cols="4">
            <v-img class="shadow" @click="addHeart(n)" aspect-ratio="1">
              <div class="font">{{ getEmoji(n) }}</div>
            </v-img>
          </v-col>
        </v-row>
      </div>
    </div>
  </div>
</template>

<script>
import EmojiIcon from "@/components/EmojiIcon.vue";

export default {
  props: ["props_height"],
  data() {
    return {
      h_count: 0,
      imgs: [],
      timer: null,
      dic_heart: {},
      obj: {}
    };
  },
  components: {
    EmojiIcon
  },
  mounted() {
    const glist = this.$refs.glist;
    const h = window.innerHeight - glist.getBoundingClientRect().y;
    glist.style.height = h + "px";

    this.$refs.bg.style.height = this.props_height + "px";
    this.$refs.items.style.height = this.props_height + "px";
    console.log(this.props_height);
  },
  methods: {
    addHeart(index) {
      this.obj["idx" + this.h_count] = {
        component: EmojiIcon,
        emoji: this.getEmoji(index)
      };
      this.h_count++;
    },
    deleteItem(payload) {
      this.$delete(this.obj, payload);
    },
    getEmoji(index) {
      switch (index) {
        case 0:
          return "😍";
        case 1:
          return "🥳";
        case 2:
          return "✌🏻";
        case 3:
          return "😺";
        case 4:
          return "🤡";
        case 5:
          return "👍🏻";
        case 6:
          return "💋";
        case 7:
          return "⭐️";
        case 8:
          return "🌹";
        default:
          return "😍";
      }
    }
  }
};
</script>

<style scoped>
.grid {
  overflow-y: scroll;
  padding: 20px;
  display: flex;
  position: absolute;
  margin-top: -20px;
  width: 100%;
}

.itmes {
  /* background-color: chocolate; */
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}

.count {
  opacity: 0;
}
.font {
  left: 0;
  right: 0;
  position: absolute;
  user-select: none;
  font-size: 40px;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.shadow {
  border-radius: 20px;
  box-shadow: 1px 1px 4px 1px lightgrey;
}
</style>